export function popup() {
   const cryptoaccess = document.querySelector(".cryptoaccess")
   if (!cryptoaccess) return
   document.addEventListener("DOMContentLoaded", () => {
      const slides = cryptoaccess.querySelectorAll(".slide")
      slides.forEach((slide) =>
         slide.addEventListener("click", (e) => {
            const button = e.target.closest(".plan__button")
            if (!button) return
            togglePopup(slide)
         })
      )
   })
   document.addEventListener("click", (e) => {
      const popupCloseButton = e.target.closest(".popup-close-button")
      if (popupCloseButton) {
         const popup = popupCloseButton.closest(".popup")
         if (popup) closePopup(popup)
      }
      if (e.target.matches(".popup")) {
         togglePopup(e.target)
      }
      if (e.target.closest('.header__nav-link')) {
         const popup = e.target.closest('.popup')
         closePopup(popup)
      }
   })
   document.addEventListener("keydown", (e) => {
      if (e.code === "Escape") {
         const activePopup = document.querySelector(".popup--open")
         if (activePopup) closePopup(activePopup)
      }
   })
   function togglePopup(slide) {
      const popup = slide.querySelector(".popup")
      if (!popup) {
         const popup = cryptoaccess.querySelector(".popup--open")
         closePopup(popup)
      } else {
         openPopup(slide)
      }
   }
   function openPopup(slide) {
      const popup = slide.querySelector(".popup")
      cryptoaccess.append(popup)
      popup.dataset.swiperSlideIndex = slide.dataset.swiperSlideIndex
      popup.classList.add("popup--open")
      document.body.classList.add("_lock")
   }
   function closePopup(popup) {
      popup.classList.remove("popup--open")
      document.body.classList.remove("_lock")
      const slide = cryptoaccess.querySelector(
         `:not(.swiper-slide-duplicate)[data-swiper-slide-index="${popup.dataset.swiperSlideIndex}"]`
      )
      slide.querySelector(".plan").append(popup)
   }

   const burgerButton = document.querySelector(".header__menu-button")
   const closeBtn = document.querySelector(".header__burger-close-button")
   const menuPopup = document.querySelector(".header__burger")
   burgerButton.addEventListener("click", (e) => {
      document.body.classList.toggle("_lock")
      document.body.classList.toggle("menu--open")
      menuPopup.classList.toggle("popup--open")
   })
   closeBtn.addEventListener("click", (e) => {
      document.body.classList.toggle("_lock")
      document.body.classList.toggle("menu--open")
      menuPopup.classList.toggle("popup--open")
   })
   document.addEventListener("keydown", (e) => {
      if (e.code === "Escape") {
         document.body.classList.remove("_lock")
         document.body.classList.remove("menu--open")
         menuPopup.classList.remove("popup--open")
      }
   })
}
