export function scrollToTop() {
   const containerWidth = 1620
   const buttonOffset = 45
   const showAtMinWidth = 992
   const showAtTopOffset = 300
   const buttonClassName = "scroll-to-top"
   const buttonVisibleClassName = "scroll-to-top--visible"
   const buttonRightOffset = 40

   const button = document.createElement("button")
   button.classList.add(buttonClassName)
   button.addEventListener("click", () => window.scrollTo(0, 0))
   document.body.append(button)

   document.addEventListener("DOMContentLoaded", updateButton)
   window.addEventListener("scroll", updateButton)
   window.addEventListener("resize", updateButton)

   let visible = false
   function updateButton() {
      updateVisibility()
      if (visible) updatePosition()
   }
   function updatePosition() {
      let buttonLeft

      if (window.innerWidth > containerWidth + buttonOffset + button.offsetWith + 20) {
         buttonLeft = (window.innerWidth - containerWidth) / 2 + containerWidth + buttonOffset
      } else {
         buttonLeft = window.innerWidth - button.offsetWidth - buttonRightOffset
      }
      button.style.left = buttonLeft + "px"
   }
   function updateVisibility() {
      if (window.innerWidth <= showAtMinWidth) {
         visible = false
         return
      }
      if (window.pageYOffset >= showAtTopOffset) {
         if (!visible) {
            button.classList.add(buttonVisibleClassName)
            visible = true
         }
      } else {
         if (visible) {
            button.classList.remove(buttonVisibleClassName)
            visible = false
         }
      }
   }
}
