import { isWebpSupported } from "./modules/functions.js"
isWebpSupported()

import { DynamicAdapt } from "./modules/dynamic-adapt.js"
const da = new DynamicAdapt("max")
da.init()

// import("./modules/popup.js")
//    .then((module) => module.popup())
//    .catch((error) => console.error("An error occurred while loading the component", error))

import { popup } from "./modules/popup.js"
popup()

import { scrollToTop } from "./modules/scrollToTop.js"
scrollToTop()


new LazyLoad({
    elements_selector: `.roadmap`,
    unobserve_entered: true,
    callback_enter: function () {
        //  console.log("importing timeline")
        import("./modules/timeline.js")
            .then((module) => {
                //    console.log("timeline module", module)
                module.timeline()
            })
            .catch((error) =>
                console.error("An error occurred while loading the timeline component", error)
            )
    },
})

new LazyLoad({
    elements_selector: `.video`,
    unobserve_entered: true,
    callback_enter: function () {
        import("./modules/video.js")
            .then(module => {
                new module.VideoSourceSelector('#video')
                module.video("#video")
            }).catch(e => console.log(e))
    },
})
